            <p style="font-size: .32rem; line-height: 18pt; text-align: center;">
                <font>境内差旅与因公出国（境）经费报销规范</font>
            </p>
            <p style="text-indent:21.0pt;line-height:18.0pt"><span style="font-size:10.5pt;&#10;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;mso-bidi-font-family:&#10;Arial;color:black">为加强财务管理，现就境内差旅与因公出国（境）经费报销规范如下：</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black"></span></p>
            <p style="text-indent:21.0pt;line-height:18.0pt"><span style="font-size:10.5pt;&#10;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;mso-bidi-font-family:&#10;Arial;color:black">总、分公司差旅应以《中国银联差旅费管理办法》（银联办发〔</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black">2016</span><span style="font-size:10.5pt;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;&#10;mso-bidi-font-family:Arial;color:black">〕</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black">211</span><span style="font-size:10.5pt;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;&#10;mso-bidi-font-family:Arial;color:black">号）、《关于进一步优化和加强差旅费管理的通知》（银联办发〔</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black">2017</span><span style="font-size:10.5pt;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;&#10;mso-bidi-font-family:Arial;color:black">〕</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black">144</span><span style="font-size:10.5pt;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;&#10;mso-bidi-font-family:Arial;color:black">号）、《中国银联因公出国（境）经费管理办法》（银联办发〔</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black">2017</span><span style="font-size:10.5pt;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;&#10;mso-bidi-font-family:Arial;color:black">〕</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black">86</span><span style="font-size:10.5pt;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;&#10;mso-bidi-font-family:Arial;color:black">号）和《关于明确银联体系员工国内差旅和因公临时出国（境）乘坐交通工具等级等事项的通知》（银联办发〔</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black">2018</span><span style="font-size:10.5pt;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;&#10;mso-bidi-font-family:Arial;color:black">〕</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black">143</span><span style="font-size:10.5pt;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;&#10;mso-bidi-font-family:Arial;color:black">号）为准，在财务报销系统</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black">“</span><span style="font-size:10.5pt;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;&#10;mso-bidi-font-family:Arial;color:black">差旅费报销单</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black">”</span><span style="font-size:10.5pt;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;&#10;mso-bidi-font-family:Arial;color:black">、</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black">“</span><span style="font-size:10.5pt;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;&#10;mso-bidi-font-family:Arial;color:black">境外差旅费报销单</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black">”</span><span style="font-size:10.5pt;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;&#10;mso-bidi-font-family:Arial;color:black">和</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black">“</span><span style="font-size:10.5pt;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;&#10;mso-bidi-font-family:Arial;color:black">涉外费用报销单</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black">”</span><span style="font-size:10.5pt;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;&#10;mso-bidi-font-family:Arial;color:black">处理相关费用的财务报销。</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black"></span></p>
