            </table>
            <p style="line-height:18.0pt"><span lang="EN-US" style="font-size:10.5pt;&#10;font-family:&quot;Arial&quot;,sans-serif;color:black"></span></p>
            <p style="text-indent:21.1pt;line-height:18.0pt"><strong><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;mso-hansi-font-family:&#10;宋体;color:black">1.</span></strong><strong><span style="font-size:10.5pt;&#10;font-family:宋体;mso-ascii-font-family:Arial;mso-bidi-font-family:Arial;&#10;color:black">异地交通费</span></strong><span style="font-size:10.5pt;mso-ascii-font-family:&#10;Arial;mso-hansi-font-family:Arial;mso-bidi-font-family:Arial;color:black">：出差人员应按照规定级别乘坐交通工具。按照人民银行总行通知要求，银联体系员工出差一律不得乘坐飞机头等舱、高铁列车商务座、轮船一等舱。</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black"></span></p>
            <p style="text-indent:21.1pt;line-height:18.0pt"><strong><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;mso-hansi-font-family:&#10;宋体;color:black">2.</span></strong><strong><span style="font-size:10.5pt;&#10;font-family:宋体;mso-ascii-font-family:Arial;mso-bidi-font-family:Arial;&#10;color:black">住宿费</span></strong><span style="font-size:10.5pt;mso-ascii-font-family:&#10;Arial;mso-hansi-font-family:Arial;mso-bidi-font-family:Arial;color:black">：根据各地经济发展水平和物价水平，分别确定各级别人员的住宿费开支标准上限。实际住宿费用在级别标准以内的，据实报销；超出标准的，按照标准报销。</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black"></span></p>
            <p style="text-indent:21.0pt;line-height:18.0pt"><span style="font-size:10.5pt;&#10;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;mso-bidi-font-family:&#10;Arial;color:#7F7F7F">一类地区：包括北京、上海、广州、深圳、天津、重庆、杭州、南京、青岛、大连、宁波、厦门。</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black"></span></p>
            <p style="text-indent:21.0pt;line-height:18.0pt"><span style="font-size:10.5pt;&#10;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;mso-bidi-font-family:&#10;Arial;color:#7F7F7F">二类地区：包括其他省会城市、珠海和三亚。</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black"></span></p>
            <p style="text-indent:21.0pt;line-height:18.0pt"><span style="font-size:10.5pt;&#10;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;mso-bidi-font-family:&#10;Arial;color:#7F7F7F">三类地区：除列示城市外的其他地区。</span><span lang="EN-US" style="font-size:&#10;10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:#7F7F7F"></span></p>
            <p style="text-indent:21.0pt;line-height:18.0pt"><span style="font-size:10.5pt;&#10;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;mso-bidi-font-family:&#10;Arial;color:black">根据各地经济发展水平和物价水平，综合考虑公司二级地市市场拓展需要，适当优化总分公司中高级管理人员以下（不含）其他人员境内差旅住宿费标准，具体如下：</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black"></span></p>
            <p style="text-indent:21.0pt;line-height:18.0pt"><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black">1.</span><span style="font-size:10.5pt;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;&#10;mso-bidi-font-family:Arial;color:black">对于住宿价格季节性变化明显的城市，公司其他人员住宿费标准在旺季可适当上浮，其中，北京上浮额度最高不超过</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black">180</span><span style="font-size:10.5pt;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;&#10;mso-bidi-font-family:Arial;color:black">元，上海、广州、深圳上浮额度最高不超过</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black">100</span><span style="font-size:10.5pt;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;&#10;mso-bidi-font-family:Arial;color:black">元。</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black"></span></p>
            <p style="text-indent:21.0pt;line-height:18.0pt"><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black">2.</span><span style="font-size:10.5pt;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;&#10;mso-bidi-font-family:Arial;color:black">考虑各省份二级地市物价水平和市场拓展需要，调整公司其他人员住宿费标准：</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black"></span></p>
            <p style="text-indent:21.0pt;line-height:18.0pt"><span style="font-size:10.5pt;&#10;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;mso-bidi-font-family:&#10;Arial;color:black">广东珠海市、佛山市、东莞市、中山市、江门市住宿费标准调整为</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black">450</span><span style="font-size:10.5pt;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;&#10;mso-bidi-font-family:Arial;color:black">元，广东其他地区住宿费标准调整为</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black">420</span><span style="font-size:10.5pt;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;&#10;mso-bidi-font-family:Arial;color:black">元。</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black"></span></p>
            <p style="text-indent:21.0pt;line-height:18.0pt"><span style="font-size:10.5pt;&#10;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;mso-bidi-font-family:&#10;Arial;color:black">江苏苏州市、无锡市、常州市、镇江市，福建泉州市、平潭综合实验区，山东淄博市、枣庄市、东营市、烟台市、潍坊市、济宁市、泰安市、威海市、日照市，云南大理州、丽江市、迪庆州、西双版纳州住宿费标准调整为</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black">380</span><span style="font-size:10.5pt;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;&#10;mso-bidi-font-family:Arial;color:black">元。</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black"></span></p>
            <p style="text-indent:21.0pt;line-height:18.0pt"><span style="font-size:10.5pt;&#10;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;mso-bidi-font-family:&#10;Arial;color:black">江苏和山东其他地区住宿费标准调整为</span><span lang="EN-US" style="font-size:&#10;10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black">360</span><span style="font-size:10.5pt;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;&#10;mso-bidi-font-family:Arial;color:black">元。</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black"></span></p>
            <p style="text-indent:21.1pt;line-height:18.0pt"><strong><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;mso-hansi-font-family:&#10;宋体;color:black">3.</span></strong><strong><span lang="EN-US" style="font-size:&#10;10.5pt;font-family:宋体;mso-ascii-font-family:Arial;mso-bidi-font-family:Arial;&#10;color:black">&nbsp;</span></strong><strong><span style="font-size:10.5pt;&#10;font-family:宋体;mso-ascii-font-family:Arial;mso-bidi-font-family:Arial;&#10;color:black">出差补贴</span></strong><span style="font-size:10.5pt;mso-ascii-font-family:&#10;Arial;mso-hansi-font-family:Arial;mso-bidi-font-family:Arial;color:black">：包括员工出差期间的伙食、交通等项费用补贴。出差补贴分为正常补贴和激励补贴。</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black"></span></p>
            <p style="text-indent:21.1pt;line-height:18.0pt"><strong><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;mso-hansi-font-family:&#10;宋体;color:black">4.</span></strong><strong><span style="font-size:10.5pt;&#10;font-family:宋体;mso-ascii-font-family:Arial;mso-bidi-font-family:Arial;&#10;color:black">其他差旅费用</span></strong><span style="font-size:10.5pt;mso-ascii-font-family:&#10;Arial;mso-hansi-font-family:Arial;mso-bidi-font-family:Arial;color:black">：包括因公出差发生的机票退改签费、火车（长途汽车、轮船）订票费、退票费、行李托运费、行李寄存费、打包费等，可凭票报销。</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black"></span></p>
            <p align="center" style="text-align:center;text-indent:21.0pt;line-height:18.0pt"><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:#D8D8D8">----------------------------------------</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black"></span></p>
            <p style="text-indent:21.0pt;line-height:18.0pt"><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black"></span></p>
            <p align="center" style="text-align:center;text-indent:28.1pt;line-height:18.0pt"><strong><span style="font-family:宋体;mso-ascii-font-family:Arial;mso-bidi-font-family:Arial;&#10;color:#31859B">因公出国</span></strong><strong><span lang="EN-US" style="font-family:&#10;&quot;Arial&quot;,sans-serif;mso-hansi-font-family:宋体;color:#31859B">(</span></strong><strong><span style="font-family:宋体;mso-ascii-font-family:Arial;mso-bidi-font-family:Arial;&#10;color:#31859B">境</span></strong><strong><span lang="EN-US" style="font-family:&#10;&quot;Arial&quot;,sans-serif;mso-hansi-font-family:宋体;color:#31859B">)</span></strong><strong><span style="font-family:宋体;mso-ascii-font-family:Arial;mso-bidi-font-family:Arial;&#10;color:#31859B">经费</span></strong><span lang="EN-US" style="font-size:10.5pt;&#10;font-family:&quot;Arial&quot;,sans-serif;color:black"></span></p>
            <p style="text-indent:21.0pt;line-height:18.0pt"><strong><span style="font-size:10.5pt;font-family:宋体;mso-ascii-font-family:Arial;mso-bidi-font-family:&#10;Arial;color:black">相关报销手续通过<span lang="EN-US">“</span>境外差旅费报销单<span lang="EN-US">”</span>和<span lang="EN-US">“</span>涉外费用报销单<span lang="EN-US">”</span>完成，具体费用内容如下：</span></strong><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black"></span></p>
            <p style="text-indent:21.0pt;line-height:18.0pt"><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black">1.</span><span style="font-size:10.5pt;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;&#10;mso-bidi-font-family:Arial;color:black">国际旅费，是指出境口岸至入境口岸旅费。</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black"></span></p>
            <p style="text-indent:21.0pt;line-height:18.0pt"><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black">2.</span><span style="font-size:10.5pt;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;&#10;mso-bidi-font-family:Arial;color:black">国外城市间交通费，是指为完成工作任务所必须发生的，在出访国家（地区）的城市与城市之间的交通费用。</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black"></span></p>
            <p style="text-indent:21.0pt;line-height:18.0pt"><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black">3.</span><span style="font-size:10.5pt;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;&#10;mso-bidi-font-family:Arial;color:black">住宿费是指在国（境）外发生的住宿费用。</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black"></span></p>
            <p style="text-indent:21.0pt;line-height:18.0pt"><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black">4.</span><span style="font-size:10.5pt;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;&#10;mso-bidi-font-family:Arial;color:black">伙食费是指在国（境）外期间的日常伙食费用。</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black"></span></p>
            <p style="text-indent:21.0pt;line-height:18.0pt"><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black">5.</span><span style="font-size:10.5pt;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;&#10;mso-bidi-font-family:Arial;color:black">公杂费是指在国（境）外期间的市内交通、邮电、办公用品、必要的小费等费用。</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black"></span></p>
            <p style="text-indent:21.0pt;line-height:18.0pt"><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black">6.</span><span style="font-size:10.5pt;mso-ascii-font-family:Arial;mso-hansi-font-family:Arial;&#10;mso-bidi-font-family:Arial;color:black">其他费用主要是指出国（境）签证费用、订退票签转费、必需的保险费、防疫费用、国际会议注册费等。</span><span lang="EN-US" style="font-size:10.5pt;font-family:&quot;Arial&quot;,sans-serif;color:black"></span></p>
        </div>
    </yd-layout>
</template>
